
.studentClass h1{
    text-align: center;
    color: rgb(155, 204, 204);
}

.student-container{
    padding: 0;
    margin-top: 2rem;
    margin-left: 5rem;
    margin-bottom: 3rem;
    margin-right: 5rem;
    border: 1px solid black;
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 10px 10px #012727;
}

.student-container input{
    background-color: transparent;
    color: teal;
}

.student-container label{
    color: rgb(155, 204, 204);
}

.student-container select {
    background-color: transparent !important;
}
.student-container button{
    background-color:#012424 ;
    color: rgb(155, 204, 204);
}


@media only screen and (max-width:600px){
    .student-container{
        margin: 1rem !important;
    }
}