.selection-Container{
    padding: 1rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 10px 10px #012727;
    padding-left: 2rem;
}
.selection-Container select{
    background-color: transparent;
    color: teal;
    outline-color: #012727;
    width: 300px;
    padding: 5px;
    margin: 5px;

}
.selection-Container Button{
    margin: 0.5rem;
    margin-left: 8rem;
    background-color:#012424 ;
    color: rgb(155, 204, 204);
}
.breadcrumb{
    display: flex;
    justify-content: center;
    background-color: #066666;
    margin-top: 1rem;
    box-shadow: 10px 10px #012727;
}

@media only screen and (max-width:600px){
    .selection-Container{
        width: max-content;
    }
    .selection-Container select{
        width: 260px;
    }
    .selection-Container Button{
        margin-left: 6rem;
    }
    .selection-Container option{
        width: max-content;
    }
}