.login-container{
    /* background-color: white; */
    width: 500px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 1rem;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 10px 10px #012727;
}
.login-container input{
    
    background-color:transparent;
    outline: none;
    color: #2ac4c457;
}
.login-container input:active{
    background-color: transparent;
}
.login-container label{
    color: #2ac4c457;
}

.login-container input::placeholder{
    color: #2ac4c457;
}
.login-container Button{
    margin-left: 12rem;
    background-color:#012424 ;
    color: rgb(155, 204, 204);
    
}




@media only screen and (max-width:600px){
    .login-container{
        width: max-content;
    }
    .login-container Button{
        background-color:#012424 ;
        margin-left: 5rem;
        
    }
}